<template>
  <div class="vb" id="play-box">
    <div
      class="Player"
      ref="videoBox"
      @touchstart.stop="Start"
      @touchmove.stop="Move"
      @touchend.stop="End"
    >
      <div class="handle-bar" v-show="showBack">
        <div class="bar-l">
          <div class="back" @click.stop="goback">
            <van-icon name="arrow-left" />
            <span>{{ name }}</span>
          </div>
        </div>
      </div>
      <Player
        ref="Player"
        :autoplay="true"
        :source="Videos"
        :type="Type"
        :IsMuted="false"
        :IsPaused.sync="IsPaused"
        :duration.sync="Duration"
        @CurrentTime="CurrentTimeChange"
        @FullScreen="Full"
        @PlayDeal="PlayDeal"
        @PlayEnded="PlayEnded"
        :IsLive="IsLive"
      />
      <!-- 兼容Android端层级问题, 弹出层被覆盖 -->
      <!-- 控制窗口 -->
      <div class="loading" v-if="loading">
        <div class="circle loader"></div>
      </div>
      <div class="volum" v-if="showV">
        <i class="iconfont icon-shengyin"></i>
        <van-slider
          :min="0"
          :max="100"
          v-model="value"
          bar-height="2px"
          active-color="#00CBAB"
        />
      </div>
      <div class="sv-control" v-if="showControl && IsControl">
        <div class="sv-play-container">
          <button class="sv-playBtn" @click="Pause" v-if="!IsPaused">
            <span class="sv-font sv-play icon-zantingtingzhi"></span>
          </button>
          <button class="sv-playBtn" @click="Play" v-if="IsPaused">
            <span class="sv-font sv-play  icon-bofang1"></span>
          </button>
          <div class="sv-time">
            <span class="sv-time-s">{{ curTime }}</span
            ><span class="sv-time-split">/</span
            ><span class="sv-time-e">{{ FormatDuration(Duration) }}</span>
          </div>
        </div>
        <div class="sv-control-r">
          <div class="sv-el-control sv-el-control-style">
            <button
              class="sv-nextBtn sv-font icon-quanping1"
              @click="FullScreenHandler"
              v-if="!isFullScreen"
            ></button>
            <button
              class="sv-nextBtn sv-font icon-quanpingshouqi"
              @click="FullScreenHandler"
              v-if="isFullScreen"
            ></button>
          </div>
        </div>
        <van-slider
          :min="0"
          :max="Duration"
          v-model="progress"
          bar-height="2px"
          active-color="#00CBAB"
          class="sv-progressBar"
          @change="SetProcess"
        />
      </div>
    </div>
  </div>
</template>

<script>
import screenfull from "screenfull";
let startPos = "";
let isScrolling = 0;
export default {
  props: {
    IsLive: { type: Boolean, default: false },
    Videos: { type: String, default: "" },
    ShowPlayList: { type: Boolean, default: true },
    showEpi: { type: Boolean, default: false },
    SelectAble: { type: Boolean, default: true },
    CanExpand: { type: Boolean, default: false },
    IsControl: { type: Boolean, default: true },
    autoFull: { type: Boolean, default: false },
    name: { type: String, default: "" }
  },
  components: {
    Player: () => import("./VPPlayer.vue")
  },
  data() {
    return {
      loading: true,
      value: 30,
      curTime: "00:00",
      url: "",
      PlayList: [],
      CurrentURL: "",
      CurrentIndex: 0,
      Type: !this.IsLive ? "mp4" : "flv",
      IsLoaded: false,
      progress: 0,
      volume: 100,
      beforeMuted: 100,
      IsPaused: false,
      Duration: 0,
      ProgressIsDrag: false,
      IsExpand: false,
      isFullScreen: false, // 是否全屏
      proess: 0,
      showControl: true,
      showBack: false,
      volumn: 0,
      showV: false,
      touchCount: 0
    };
  },
  mounted() {
    screenfull.on("change", () => {
      if (!this.isFullScreen) {
        this.showBack = true;
        document.querySelector(".vpplayer").style.height = "100%";
      }
      if (this.isFullScreen && this.IsControl) {
        document.querySelector(".vpplayer").style.height = "5.6rem";
      }
      this.isFullScreen = screenfull.isFullscreen;
    });
    this.timerC = setTimeout(() => {
      this.loading = false;
    }, 5000);
  },
  beforeDestroy() {
    screenfull.off("change");
    clearTimeout(this.timerC);
    this.Dispose();
  },
  watch: {
    touchCount(val) {
      clearTimeout(this.timerA);
      clearTimeout(this.timerB);
      this.timerA = null;
      this.timerB = null;
      if (this.IsControl) {
        this.showControl = true;
      }
      if (this.isFullScreen) {
        this.showBack = true;
      }
      this.timerA = setTimeout(() => {
        this.showControl = false;
        this.showBack = false;
      }, 3000);
    }
  },
  methods: {
    PlayEnded() {
      this.loading = true;
    },
    PlayDeal() {
      console.log("deal");
      this.loading = false;
      if (this.IsControl) {
        this.timerB = setTimeout(() => {
          this.showControl = false;
        }, 1000);
      }
    },
    goback() {
      if (this.IsControl) {
        this.FullScreenHandler();
        this.showControl = true;
        this.showBack = false;
      } else {
        this.url = "";
        this.$router.push("/home-source");
      }
    },
    Full() {
      if (this.autoFull) {
        console.log("全屏");
        this.FullScreenHandler();
      }
    },
    // 全屏
    FullScreenHandler() {
      if (screenfull.isEnabled) {
        screenfull.toggle(this.$refs.videoBox);
      }
    },
    // 切换静音
    ChangeMuted() {
      if (this.volume > 0) {
        this.beforeMuted = this.volume;
        this.volume = 0;
      } else {
        this.volume = this.beforeMuted;
      }

      this.SetVolume(this.volume);
    },
    // 控制音量
    SetVolume(val) {
      if (this.$refs.Player) {
        this.$refs.Player.Volume(val);
      }
    },
    // 格式化时长
    FormatDuration(val) {
      var du = Number(val) / 60;
      var fl = du - parseInt(du);
      fl = fl * 60;
      du = parseInt(du);
      fl = parseInt(fl);
      if (parseInt(fl / 10) === 0) {
        fl = "0" + fl;
      }
      if (parseInt(du / 10) === 0) {
        du = "0" + du;
      }
      var s1 = du + ":" + fl;
      return s1;
    },
    // 更新进度
    CurrentTimeChange(val) {
      this.progress = val;
      this.curTime = this.FormatDuration(val);
    },
    // 控制进度
    SetProcess(val) {
      if (this.$refs.Player) {
        this.$refs.Player.CurrentTime(val);
      }
    },
    // 播放
    Play() {
      if (this.$refs.Player) {
        this.$refs.Player.Play();
      }
    },
    // 暂停
    Pause() {
      if (this.$refs.Player) {
        this.$refs.Player.Pause();
      }
    },
    // 释放
    Dispose() {
      try {
        if (this.$refs.Player) {
          console.log("释放");
          this.$refs.Player.Pause();
          this.$refs.Player.Dispose();
        }
      } catch (ex) {
        console.log(ex);
      }
      this.progress = 0;
      this.Duration = 0;
    },
    Start(e) {
      // sv-nextBtn sv-font icon-quanping
      var touch = event.targetTouches[0];
      startPos = { x: touch.pageX, y: touch.pageY };
      let range = startPos.x / document.documentElement.clientWidth;
      var attr = e.target.getAttribute("class");
      if (attr === "vjs-tech" && range < 0.8) {
        this.touchCount++;
      }
      if (attr === "sv-progressBar van-slider") {
        this.touchCount++;
      }
    },
    End() {
      this.timerC = setTimeout(() => {
        this.showV = false;
      }, 1000);
    },
    Move() {
      var touch = event.targetTouches[0];
      var endPos = { x: touch.pageX - startPos.x, y: touch.pageY - startPos.y };
      isScrolling = Math.abs(endPos.x) < Math.abs(endPos.y) ? 1 : 0; // isScrolling为1时，表示纵向滑动，0为横向滑动
      if (isScrolling === 0) {
        event.preventDefault();
        this.touchCount++; // 阻止触摸事件的默认行为，即阻止滚屏
      } else {
        event.preventDefault();
        clearTimeout(this.timerC);
        this.timerC = null;
        this.showControl = false;
        this.showV = true;
        if (startPos.y < touch.pageY) {
          this.value = this.value - Math.abs(endPos.y) / 10;
        } else {
          this.value = Math.abs(endPos.y) / 10 + this.value;
        }
        if (this.value > 100) {
          this.value = 100;
        }
        this.$refs.Player.Volume(this.value);
      }
    }
  }
};
</script>

<style lang="less">
@import url("./handle.less");
.vb {
  width: 100%;
  position: relative;
  height: 210px;
  @BorderColor: rgba(0, 0, 0, 0.75);
  z-index: 10;
  .loading {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .icon-jietu {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 15px;
    z-index: 100000;
    color: #fff;
    font-size: 24px;
  }
  .circle {
    display: inline-block;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background: whiteSmoke;
    box-shadow: 4px -40px 60px 5px #00cbab inset;
  }
  .loader {
    background: linear-gradient(to right, #00cbab 50%, transparent 50%);
    animation: spin 1s infinite linear;
  }
  .loader:before {
    display: block;
    content: "";
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 45px;
    height: 45px;
    background: #000;
    border-radius: 50%;
  }
  .spin {
    animation: spin 1s infinite linear;
  }
  @keyframes spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @font-face {
    font-family: "iconfont"; /* project id 1867770 */
  }
  .volum {
    position: absolute;
    display: flex;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 150px;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 5px;
    padding: 4px;
    i {
      margin-right: 10px;
    }
    .van-slider {
      margin-top: 9px;
      width: 120px;
    }
  }
  .van-slider__button {
    width: 14px;
    height: 14px;
  }
  .sv-target video {
    background-color: #000000;
  }

  .sv-font {
    font-family: "iconfont";
  }

  .sv-pic-pic {
    color: #ffffff;
    font-size: 20px;
    margin-right: 6px;
  }

  .sv-play {
    color: #ffffff;
    font-size: 20px;
  }

  .sv-fontBtn {
    color: #ffffff;
    font-size: 20px;
  }

  .sv-next {
    color: #ffffff;
    font-size: 20px;
  }

  .icon-quanping1,
  .icon-quanpingshouqi,
  .icon-shengyin {
    color: #ffffff;
    font-size: 20px;
  }

  .sv-cancelFull {
    color: #ffffff;
    font-size: 20px;
  }

  .sv-target {
    position: relative;
  }
  .sv-control {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 42px;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .sv-play-container {
    height: 100%;
    /* background-color: royalblue; */
    display: flex;
    flex-direction: row;
    padding-right: 10px;
  }
  .sv-control-r {
    height: 100%;
    /* background-color: royalblue; */
    display: flex;
    flex-direction: row;
    padding-left: 10px;
    padding-right: 10px;
  }
  .sv-play-container button.sv-playBtn {
    background: none;
    border: none;
    cursor: pointer;
    padding: 0 10px;
    outline: none;
    color: inherit;
    text-align: inherit;
    font: inherit;
    line-height: inherit;
    margin-left: 10px;
  }

  .sv-control-r button.showMute {
    background: none;
    border: none;
    cursor: pointer;
    padding: 0 10px;
    outline: none;
    color: inherit;
    text-align: inherit;
    font: inherit;
    line-height: inherit;
    position: relative;
  }

  .sv-time {
    height: 100%;
    color: #ffffff;
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: 12px;
  }

  .sv-time-split {
    padding: 0 4px;
  }
  .sv-control-r button.sv-mute-button {
    position: absolute;
    top: 0;
    left: -4.5px;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    z-index: 10;
    background-color: #00a1d6;
    border: 0;
    cursor: pointer;
    outline: none;
  }

  .sv-progressBar {
    position: absolute;
    top: 0;
    left: 10px;
    width: calc(100vw - 20px);
    height: 2px;
    background-color: hsla(0, 0%, 100%, 0.2);
    border-radius: 4px;
    cursor: pointer;
  }
  .hide {
    display: none !important;
  }

  .sv-full-screen {
    position: fixed !important;
    width: 100% !important;
    height: 100% !important;
    left: 0 !important;
    top: 0 !important;
    margin: 0 !important;
    padding: 0 !important;
  }

  .sv-loading {
    /* width: 60Px;
    height: 60Px; */
    position: absolute;
    left: 48%;
    top: 48%;
  }
  .sv-loading span {
    display: inline-block;
    width: 4px;
    height: 100%;
    border-radius: 4px;
    background: #00a1d6;
    -webkit-animation: load 1s ease infinite;
    animation: load 1s ease infinite;
  }
  @-webkit-keyframes load {
    0%,
    100% {
      height: 20px;
      background: #00a1d6;
    }
    50% {
      height: 40px;
      margin: -15px 0;
      background: lightblue;
    }
  }
  .sv-loading span:nth-child(2) {
    -webkit-animation-delay: 0.2s;
    animation-delay: 0.2s;
  }
  .sv-loading span:nth-child(3) {
    -webkit-animation-delay: 0.4s;
    animation-delay: 0.4s;
  }
  .sv-loading span:nth-child(4) {
    -webkit-animation-delay: 0.6s;
    animation-delay: 0.6s;
  }
  .sv-loading span:nth-child(5) {
    -webkit-animation-delay: 0.8s;
    animation-delay: 0.8s;
  }
  .sv-el-control-style {
    padding-right: 10px;
    padding-left: 10px;
    height: 100%;
    display: flex;
    flex-direction: row;
  }

  .sv-nextBtn {
    background: none;
    border: none;
    cursor: pointer;
    outline: none;
    text-align: inherit;
  }

  .sv-active {
    color: #00a1d6 !important;
  }

  .activeColor {
    box-sizing: border-box;
    border: 2px solid #ffff;
  }
}

.vb:hover {
  .controlbar {
    display: block;
  }
}
</style>



